import { Component, OnInit } from "@angular/core";
import { FormBuilder, FormGroup, Validators } from "@angular/forms";
@Component({
  selector: "ngx-form-layouts",
  styleUrls: ["./form-layouts.component.scss"],
  templateUrl: "./form-layouts.component.html",
})
export class FormLayoutsComponent implements OnInit {
  enterpriseForm: FormGroup;

  constructor(private formBuilder: FormBuilder) {}

  ngOnInit(): void {
    this.initForm();
  }

  initForm(): void {
    this.enterpriseForm = this.formBuilder.group({
      inputemail: ["", [Validators.required, Validators.email]],
      inputname: ["", Validators.required],
      inputphone: ["", Validators.required],
      inputpmomo: ["", Validators.required],
      inputom: ["", Validators.required],
      inputcontrib: ["", Validators.required],
      inputpassword: ["", [Validators.required, Validators.minLength(6)]],
    });
  }

  onSubmit(): void {
    if (this.enterpriseForm.valid) {
      const formData = this.enterpriseForm.value;
      // Here, you can send the formData to your API for creation or update
      console.log("Form data:", formData);
    }
  }
  isFieldInvalid(fieldName: string): boolean {
    const control = this.enterpriseForm.get(fieldName);
    return control
      ? control.invalid && (control.dirty || control.touched)
      : false;
  }

  getErrorMessage(fieldName: string): string {
    const control = this.enterpriseForm.get(fieldName);
    if (control && control.errors) {
      if (control.errors.required) {
        return "This field is required";
      }
      if (control.errors.email) {
        return "Invalid email format";
      }
      if (control.errors.minLength) {
        return `Minimum length is ${control.errors.minLength.requiredLength}`;
      }
      // Add more error messages for different validators as needed
    }
    return "";
  }
}
